.progress-bar-outline {
  border-radius: 20px;
  overflow: hidden;
  border: 2px solid black;
  width: 100%;
  height: 40px;
}

div[id*="progress-bar"] {
  background-color: red;
  height: 100%;
  width: 0;
  border-radius: 20px;
  transition: width 0.5s ease-in-out;
}

div:hover [id*="progress-bar"] {
  width: 200px;
}

#dashboard {
  h3 {
    margin-bottom: 10px;
  }
}